* {
    box-sizing: border-box;
}

::selection {
    background-color: $clr1;
    color: $clr6;
}

select::-ms-expand {
    display: none;
}

html {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
        "Helvetica Neue", sans-serif;
    line-height: 1.6;
}

body {
    margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0 0;
    line-height: 1;
}

img {
    width: 100%;
}
p {
    margin: 0 0;
}

a {
    color: $clr1;
    text-decoration: none;
}

a:hover {
    color: $clr2;
}

button:active,
button:focus {
    outline: 0;
}

* {
    transition: background-color 0.3s, box-shadow 0.3s;
}

textarea {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
        "Helvetica Neue", sans-serif;
}

.main-layout {
    max-width: 700px;
    margin: 0 auto;
}
